@use '../helpers/' as *;

.clients {
  &__body {
    width: 88%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);

    @include laptop {
      width: 100%;
    }

    @include mobile {
      row-gap: fluid(60, 40);
    }
  }

  &__content {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: fluid(100, 60);
    margin-bottom: rem(60);

    @include laptop {
      column-gap: fluid(60, 40);
      margin-bottom: rem(20);
    }

    @include mobile {
      grid-template-columns: 1fr;
      grid-template-columns: 100%;
      margin-bottom: 0;
    }
  }

  &__text-slider {
    &-wrapper {
      width: 100%;
      display: flex;
      position: relative;
      overflow: hidden;
    }

    &-item {
      display: flex;
      flex-direction: column;
      row-gap: fluid(40, 20);
      transition: opacity 1s ease-in-out;
    }
  }
  
  &__slider {
    width: 100%;
    aspect-ratio: 5 / 3;
    position: relative;
    overflow: hidden;
    
    @include mobile-s {
      aspect-ratio: 4 / 3;
    }
  }

  &__video-container {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    background-color: var(--background-basic);
    overflow: hidden;
    z-index: 0;
  }

  &__video {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    display: block;
    pointer-events: none;
    transform: translateZ(0);
    backface-visibility: hidden;
    z-index: 1;
    mix-blend-mode: lighten;
    
    // Исправляем глюк "чёрного экрана" при autoplay на iPhone
    &::-webkit-media-controls {
      display: none !important;
    }

    &::-webkit-media-controls-start-playback-button {
      display: none !important;
      -webkit-appearance: none;
    }

    @include mobile-s {
      height: 100%;
    }
  }

  &__slider-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    inset: 0;
    z-index: 2;
  }

  &__slider-list {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    transition: var(--slide-transition);
    
  }

  &__slider-item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    opacity: 0.4;
    transition: opacity 0.5s var(--slide-transition), transform 2s var(--slide-transition);
    // z-index: 2;

    &.swiper-slide-active {
      opacity: 1;
      transform: scale(1);
      z-index: 10;

      .clients__slider-image {
        cursor: pointer;
        z-index: 11;
        transition: transform var(--slide-transition) ease-in-out;
        pointer-events: auto;
      }
    }

    &.swiper-slide-next,
    &.swiper-slide-prev {
      transform: scale(0.5);
    }
  }

  &__slider-image-wrapper > * {
	  position: absolute;
    inset: 0;
	  perspective: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
	  transform-style: preserve-3d;
    z-index: 3;
    pointer-events: none;
  }

&__slider-image {
  max-width: rem(160);
  width: 100%;
  height: auto;
  position: relative !important; 
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  aspect-ratio: 3 / 1;
  transform: translateZ(0);
  backface-visibility: hidden; 
  z-index: 11;
  pointer-events: auto;
  user-select: none;
  -webkit-tap-highlight-color: transparent; // убирает серый флеш при тапе
  touch-action: manipulation; // оптимизирует отклик на tap
  
  @supports not (aspect-ratio: 3 / 1) {
    &::before {
      content: "";
      display: block;
      padding-top: calc(100% / 3); // 3:1 пропорции
    }
  }

  @include laptop {
    max-width: 160px;
  }
    
  @include mobile {
    max-width: 140px;
  }

  &--asml {
    background-image: url('../img/logos/asml.png');
  }

  &--iwc {
    background-image: url('../img/logos/iwc.png');
  }

  &--bb {
    background-image: url('../img/logos/bb.png');
  }

  &--rioleringsdiensten {
    background-image: url('../img/logos/iwc.png')
  }

  &--ministerie {
    background-image: url('../img/logos/bb.png')
  }

  &--strik {
    background-image: url('../img/logos/bb.png')
  }

  &--bouw {
    background-image: url('../img/logos/bb.png')
  }

  &--water {
    background-image: url('../img/logos/bb.png')
  }

  &-left {
    perspective-origin: left center;
    clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%); 
    .clients__slider-image {
      animation: image-left 1s ease-in-out infinite;
      animation-direction: alternate;
    }
  }

  &-right {
    perspective-origin: right center;
    clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
    // Фикс вертикальной полосы на некоторых дисплеях
    margin-left: -.55px;
  
    .clients__slider-image {
      animation: image-right 1s ease-in-out infinite;
      animation-direction: alternate;
    }
  }
}

  &__facts {
    width: 100%;
    margin-bottom: fluid(60, 20);
  }

  &__reviews {
    width: 100%;
    display: flex;
    flex-direction: column;
  } 
}



